.Avatar__img {
	object-fit: cover;
}

.cell_prepods .Cell__bottom {
	margin-top: 2px !important;
}

.span_title {
	color: #000;
	font-weight: 500;
}

.welcome_banner .Banner__in {
	padding: 20px 16px !important;
}

.CardScroll--ios .Card--sz-l {
    width: calc(100% - 40px);
    margin-right: 15px;
}

.card-rasp{
	background: none !important;
	margin-bottom: 50px;
}

.card-rasp__div {
	min-height: calc(100vh - 230px);
}

.card-item-lesson {
	width: 100% !important;
}

.card-rasp__div .CardGrid {
	padding: 0px !important;
}

.card-rasp__div-title {
	margin: 0px;
	width: 100%;
	display: flex;
	justify-content: center;
	font-size: 20px;
	font-weight: 500;
}

.name_lesson {
	padding: 0px;
	margin: 0px;
}

.card-item-lesson .Cell__main {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	padding-left: 15px;
}

.time-lesson {
	margin: 0px;
	font-size: 15px;
}

.card-item-lesson .Cell__before {
    display: flex;
    align-items: center;
    justify-content: center;
    /*align-self: stretch;*/
 }

.card-item-lesson .Cell__before-in {
    align-self: center;
}

.day_name {
	margin: 0px;
	padding-bottom: 5px;
	padding-left: 5px;
	font-size: 18px;
	font-weight: 500;
	opacity: .7;
}

.link-url {
	min-width: 100%;
}

.banner_soon .Banner__header, .Banner__subheader {
	color: #000 !important;
	font-weight: 700;
}

.banner_soon .Banner__bg {
	opacity: 1;
	filter: blur(4px);
}